import 'hack'
import React from 'react'
import styled, { ThemeProvider, injectGlobal } from 'styled-components'
import theme, { inverted } from './theme'
import { Link, Button } from '../components'
import me from './me.jpg'
import Reaction from '../components/Reaction'

injectGlobal`
  body {
    margin: 0;
    font-family: serif;
    background-color: ${theme.background};
    color: ${theme.main};
    font-size: 17px;
    line-height: 28px;
  }
`
const HeaderText = styled.div`
  padding-left: 20px;
`
const Description = styled.div`
  font-style: italic;
  line-height: 1.35em;
`
const Me = styled.img.attrs({
  src: me
})`
  width: 60px;
  height: 60px;
  border-radius: 30px;
`
const Header = styled.div`
  margin: 2em 0;
  display: flex;
  flex-direction: row;
`
const Container = styled.div`
  display: flex;
  flex-direction: column;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
`
const FooterContainer = styled.div`
  padding: 20px 5%;
  background-color: ${theme.main};
  color: ${theme.background};
`
const Main = styled.div`
  margin: 0 5% 40px;
  flex-grow: 1;
  max-width: 54em;
`

export default ({ data, children }) => {
  const { site: { siteMetadata: { title, description, email } }} = data
  return (<ThemeProvider theme={theme}>
    <Container className='hack'>
      <Main>
        { Reaction && <Reaction.component /> }
        <Header>
          <Me/>
          <HeaderText>
            <Link to='/' className='media-heading'>{title}</Link>
            <Description>{description}</Description>
          </HeaderText>
        </Header>
        {children()}
      </Main>
      <ThemeProvider theme={inverted}>
        <FooterContainer>
          mailto: <a href={`mailto:${email}`}>{email}</a>
        </FooterContainer>
      </ThemeProvider>
    </Container>
  </ThemeProvider>)
}
export const query = graphql`
  query LayoutQuery {
    site {
      siteMetadata {
        title
        description
        email
      }
    }
  }
`



